.footer {
  display: none;
}

.fp-viewing-lastPage {
  .footer {
    display: none;
  }
}

@media (min-width: 1500px ) {
  .fixed__bottom {
    position: fixed;
    bottom: 0;
    width: 100%;
    transition: all 0.4s ease;
  }
  .footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 1em 2em 1em;
    border-top: 3px solid $white;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(12px);
    background-color: $background;

    .footer__address {
      width: 25%;
      .bold {
        font-weight: bold;
      }
    }
    .footer__map_debugger {
      max-width: 30%;
      margin-left: 4em;
      margin-right: 1em;
      .footer_map {
        .map__picture {
          max-width: 100%;
          height: 100px;
        }
      }
      .debugger {
        text-align: center;
      }
    }
    .footer__menu {
      width: 20%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      padding: 0;
      margin: 0;
      li {
        display: block;
        list-style: none;
        font-size: px-to-rem(42px);
        padding: .2em 1em;
      }
      a{
        &:hover, &:focus {
          color: $active;
          outline: none;
          transition-property: all;
          transition-duration: .5s;
        }
      }
    }
    .developer {
      padding: 14px 0;
      display: block;
      width: 10%;
      a {
        display: inline-block;
        transition: all .5s ease 0s;
        p {
          display: inline-block;
          margin: 0;
          padding: 0;
          text-transform: uppercase;
          font-size: px-to-rem(16px);
        }
        &:hover {
          background-color: inherit;
          color: $light-active;
          transition: all .5s ease 0s;
        }
      }
    }
  }
}