.header {
  background: url("../img/svg/logo-jeep.svg") center no-repeat;
  background-size: 100%;
  position: relative;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: .9;
  }
  a {
    transition: all .5s ease 0s;
    &:hover, &:focus {
      background-color: $active;
      transition-property: all;
      transition-duration: .5s;
    }
  }


  .header__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;

    li {
      list-style: none;
      display: block;
      font-size: px-to-rem(24px);
      line-height: px-to-rem(28px);
      border: 1px solid $active;
      margin-bottom: 1em;
    }
  }

  .header__item_sto {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(28px);
  }

  .item_icon {
    height: 2em;
    padding-right: 1em;
  }

  .item_text {
    display: block;
    margin: 0 auto;
    text-align: center;
  }
}

@media (min-width: 640px) {
.header{
  .header__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;

    li {
      flex-basis: 18em;
      font-size: px-to-rem(28px);
      line-height: px-to-rem(32px);
    }
    .header__item_sto {
      font-size: px-to-rem(36px);
    }
  }

  .item_icon {
    margin-right: 1em;
    margin-left: 1em;
  }
}
}

@media (min-width: 1024px) {
  .header {
    background-size: contain;

    .header__list {
      li {
        flex-basis: 15em;
        font-size: px-to-rem(32px);
        line-height: px-to-rem(54px);
        margin: 0 1.2em 1em;

        .header__item_sto {
          font-size: px-to-rem(40px);
        }

       .item_shop {
         line-height: px-to-rem(28px);
        }
      }
    }
  }
}

@media (min-width: 1500px) {
  .header {
    .header__list {
      li {
        flex-basis: 15em;
        font-size: px-to-rem(36px);
        line-height: px-to-rem(62px);

        .header__item_sto {
          font-size: px-to-rem(42px);
        }

        .item_shop {
          line-height: px-to-rem(32px);
        }
      }
    }

  }

}

@media (min-width: 1850px) {
.header {
  .header__list {
    li {
      font-size: px-to-rem(52px);
      line-height: px-to-rem(82px);

      .header__item_sto {
        font-size: px-to-rem(62px);
      }

      .item_shop {
        line-height: px-to-rem(42px);
      }
    }
  }
}
}

@media (max-height: 768px) {
 .header {
   .title {
     padding-top: .5em;
     margin-bottom: .5em;
   }
   .header__list li {
     margin-bottom: .5em;
   }
 }
}