@charset "UTF-8";

/**
 * Основные стили
 **/
* {
	box-sizing: border-box;
}

html {
	font-size: 16px;
	color: $white;
	background: $background;
}

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	font-style: italic;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	@include min-max(320px, 100%, 0);
}

/**
 * my styles
 **/
.wrap {
	//@include min-max(320px, 1140px, 15px);
	@include min-max(320px, 2048px, 15px);
}

.uppercase {
	text-transform: uppercase;
}

a {
	text-decoration: none;
	color: inherit;
	box-sizing: border-box;
	transition: all .5s ease 0s;

	&:hover, &:focus {
		outline: none;
		transition-property: all;
		transition-duration: .5s;
	}
}

.btn {
	display: flex;
	box-sizing: border-box;
	padding: 1rem 2rem;
	align-items: center;
	justify-content: space-between;
	cursor: pointer;

	&:hover, &:focus {
		outline: none;
	}
}


// z-index

.top__menu_wrap {
	z-index: 9;
	.dropdown {
		.dropdown__content {
			z-index: 15;
		}
	}
}

.fixed {
	z-index: 10;
	.menu__btn {
		z-index: 1;
	}
	.menu__box {
		z-index: -2;
	}
}

.scroll {
	z-index: 8;
}
.scroll-button {
	z-index: 7;
}
.footer {
	z-index: 11;
}



