.fixed {
  position: sticky;
  top: 0;
  width: 100%;
  max-height: 100px;
  box-sizing: border-box;
  border-top: 3px solid $white;
  border-bottom: 3px solid $white;
  padding-top: 4px;
  padding-bottom: 4px;
  margin-top: 35px;
  color: $background;
  background-color: $background;
  transition: all 0.4s ease;

  a:hover, :focus {
    background-color: inherit;
  }
}


#firstPage, #secondPage, #thirdPage, #fourthPage, #lastPage  {
  .fixed {
    position: fixed;
    top: 0;
    width: 100%;
    margin-top: 0;
    transition: all .8s ease 0s;
  }
}

.fixed__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.fixed__phone {
  a {
    display: block;
  }
}

.fixed__logo {
  a {
    img {
      height: 2.2rem;
      width: auto;
    }
  }
}

.fixed__menu {
  position: relative;

  .menu_text {
    display: none;
  }

  .fixed__menu_hamburger {
    display: block;
    width: 2rem;
    height: 2rem;
  }

  #menu__toggle {
    opacity: 0;
    position: absolute;
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
    transform-origin: 90% 90%;
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    width: 32px;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    left: 0;
  }

  .menu__btn {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 32px;
    width: 32px;
  }

  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    position: absolute;
    display: block;
    width: 32px;
    height: 3px;
    bottom: 8px;
    background-color: $white;
    transition-duration: .25s;
  }
  .menu__btn > span::before {
    content: '';
    top: -8px;
    width: 28px;
    right: 0;
  }
  .menu__btn > span::after {
    content: '';
    top: -16px;
    width: 26px;
    right: 0;
  }

  .menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width:100%;
    height: 100%;
    margin: 0;
    padding: 120px 0 1em 0;
    list-style: none;
    background-color: $background;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
    transition-duration: .25s;
  }

  .menu__item {
    display: flex;
    align-items: center;
    font-size: px-to-rem(24px);
    font-weight: bold;
    font-style: italic;
    text-transform: uppercase;
    padding: .7em;
    color: $white;
    border-top: 1px solid $active;
    transition-duration: .25s;
    margin-top: -1px;
    &:first-child {
      border-top: 0;
    }
    &:last-child {
      border-bottom: 1px solid $active;
    }
  }
  .menu__item:hover {
    background-color: $active;
  }

  .item_icon{
    display: block;
    height: 2em;
    margin-right: 1em;
  }
}

.fixed__coordinates {
  display: none;
}

@media (max-height: 435px) {
  .fixed__menu{
    .menu__box{
      padding-top: 110px;
      overflow: scroll;
    }
    .menu__item {
      font-size: px-to-rem(11px);
    }
  }
}


@media (min-width: 640px ) {
.fixed {
  .fixed__wrap {
    padding: 0;
  }

  .fixed__phone {
    display: none;
  }
  .fixed__menu, .fixed__coordinates {
    display: flex;
    height: 2.3rem;
    flex-basis: 26%;
    align-items: center;
    justify-content: flex-end;
    font-size: px-to-rem(18px);
    font-weight: bold;
  }

  .fixed__menu {
    order: 1;
    background: linear-gradient(130deg, $white 84%, transparent 1%);

    position: relative;

    &:before {
      content: "";
      display: block;
      position: absolute;
      bottom: 10%;
      left: 0;
      width: 80%;
      border-bottom: 3px solid $background;
    }

    .menu_text {
      display: block;
      text-transform: uppercase;
      padding-right: .3em;
    }
  }
  .fixed__menu_hamburger {
    margin-right: 20%;
    .menu__btn > span,
    .menu__btn > span::before,
    .menu__btn > span::after {
      background-color: $background;
    }
  }

  .fixed__logo {
    order: 2;
    a {
      img {
        height: 2.7rem;
      }
    }
  }

  .fixed__coordinates {
    background: linear-gradient( 310deg, $white 83%, transparent 1%);
    order: 3;
    height: 2.5rem;
    justify-content: flex-start;
    position: relative;

    &:before {
      content: "";
      display: block;
      position: absolute;
      bottom: 10%;
      right: 0;
      width: 80%;
      border-bottom: 3px solid $background;
    }

    .coordinates {
      flex-direction: row;
      align-items: center;
      margin-left: 20%;
    }

    .coordinates_text {
      display: inline-block;
      padding-right: .3em;
      text-align: left;
      transform: translateY(-10%);
    }

    .coordinates_icon {
      display: inline-block;
      height: 1.2em;
      transform: translateY(5%) rotate(5deg);
    }
  }
}
}

@media (min-width: 1024px ) {
.fixed {
    .fixed__coordinates {

      .coordinates_icon {
        transform: translateY(10%) rotate(5deg);
      }

    }

    .menu__box {
      width: 33.3%;
    }
  }
}