.social__networks {
  list-style: none;
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  justify-content: center;

  li {
    font-size: px-to-rem(42px);
    margin-right: .4em;
  }
  li:last-child {
    margin-right: 0;
  }

  a{
    &:hover, &:focus {
      color: $active;
      outline: none;
      transition-property: all;
      transition-duration: .5s;
    }
  }
}
@media (min-width: 768px) {
  .social__networks {
    flex-direction: column;
    position: absolute;
    top: 25%;
    left: 1em;
    li {
      padding: 0;
      margin: 0 0 .3em 0;
    }
    li:last-child{
      margin: 0;
    }
  }
}

@media (min-width: 1024px) {
  .section__shop {
    .social__networks {
      top: 40%;
    }
  }
}
