.scroll {
  display: none;
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .scroll {
    display: block;
    position: fixed;
    right: 1em;
    top: 40%;
    transform: translateY(-50%);
    list-style: none;
    margin: 0;
    padding: 0;
    counter-reset: counter;
  }
  .scroll__item {
    position: relative;
    margin-bottom: px-to-rem(14px);
    &:before {
      counter-increment: counter;
      content: "0"counter(counter);
      position: absolute;
      left: -1.5em;
      top: 50%;
      transform: translateY(-50%);
      color: $white;
      font-size: px-to-rem(12px);
      line-height: px-to-rem(14px);
      opacity: 0;
    }

    a {
      width: px-to-rem(8px);
      height: px-to-rem(8px);
      border: px-to-rem(1px) solid $active;
      border-radius: 50%;
      display: block;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  .scroll__item.active {
    &:before {
      opacity: 1;
    }

    a {
      background-color: $white;
      width: px-to-rem(4px);
      height: px-to-rem(4px);
      margin: 0 auto;
      border: none;
    }
  }
}


@media (max-height: 435px){
  .scroll {
    display: none;
  }
}
