.scroll-button {
  display: none;
  border: none;
  margin: 0;
  padding: 0;
  &:hover, &:focus, &:visited, &:active {
    outline: none;
    border: none;
  }
}

.fp-viewing-lastPage {
  .scroll-button {
    display: none;
  }
}

@media (min-width: 768px) {
 .scroll-button {
   display: flex;
   flex-direction: column;
   justify-content: center;
   position: fixed;
   right: 0;
   top: calc(40% + 9em);
   transform: translateY(-50%);
   background-color: $white;
   box-sizing: border-box;
   cursor: pointer;
   padding: .5em .3em 3em;
   border-radius: .3em;
   clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%, 0 75%);

   .scroll-button_img {
     height: 100%;
     width: 100%;
   }

   span {
     display: block;
     color: $active;
     font-size: px-to-rem(14px);
     line-height: px-to-rem(16px);
     font-weight: bold;
     text-align: left;
     transform: rotate(-90deg);
     margin-top: 1.5em;
   }
 }
}

@media (max-height: 435px){
  .scroll-button {
    display: none;
  }
}