.section__shop {
  background: url("../img/svg/basket.svg") no-repeat center;
  background-size: contain;
  position: relative;
  padding-top: 1em;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: .9;
  }

  a {
    transition: all .5s ease 0s;
    &:hover, &:focus {
      outline: none;
      color: $active;
      background-color: transparent;
      transition-property: all;
      transition-duration: .5s;
    }
  }
}

.shop__list, .trading-platforms__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  color: $active;
}

.shop__list {
  margin-bottom: 1em;
}

.item {
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  font-size: px-to-rem(24px);
  font-weight: bold;
  background-color: $white;
  border: 1px solid $active;
  padding: .5em;
  margin-bottom: 1em;
}

.item_text {
  padding: .5em 0;
  margin: 0;
}

.trading-item {
  list-style: none;
  font-size: px-to-rem(42px);
  color: white;
  margin-bottom: .5em;
}

@media (min-width: 480px) {
  .shop__list, .trading-platforms__lis {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .item {
      width: 80%;

      .item_icon {
        display: block;
        height: px-to-rem(70px);
      }
    }

  .item_text {
    font-size: px-to-rem(32px);
  }
}

}
@media (min-width: 640px) {
  .shop__list, .trading-platforms__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    .item {
      flex-basis: 18em;
      font-size: px-to-rem(28px);
      line-height: px-to-rem(32px);
      margin: 1em;
    }
  }

  .trading_btn{
    font-size: 3rem;
    padding: 0 1em;
  }
}

@media (min-width: 1024px){
  .section__shop {
    background: url("../img/svg/logo-jeep.svg") center no-repeat;
    background-size: contain;
  }
  .trading_btn{
    font-size: 3.5rem;
    margin-bottom: 1em;
  }

}

@media (min-width: 1150px) {
  .shop__list {
    margin-left: 2em;
    margin-right: 1em;
  }
}

@media (max-height: 768px) {
  .section__shop {
    .title {
      padding-top: .5em;
    }
    .shop__list {
      .item {
        margin-top: 0;
      }
    }
  }
}




