.section__contacts {
  display: flex;
  flex-direction: column;
  align-items:center;
}

.contacts__content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 290px;
}

.contacts__info {
  background-color: $active;
  border-radius: 1.5em;
  padding: 1em;
  margin-bottom: 3em;
  font-size: px-to-rem(18px);
  line-height: px-to-rem(21px);

  p{
    padding: 0;
    &:last-child{
      margin: 0;
    }
  }
}
.contacts__time-work {
  color: $background;
  font-weight: bold;
  text-align: center;
  margin-top: 0;
}
.contacts__phone, .contacts__mail, .contacts__address {
  span {
    display: block;
    color: $black;
    font-weight: bold;
    padding-left: 1.5rem;
    margin-bottom: .3em;
    background: url("../img/svg/tel.svg") left center no-repeat;
  }
  a {
    display: inline-block;
    padding-left: 1rem;
    transition: all .5s ease 0s;

    &:hover {
      background-color: inherit;
      color: $light-active;
      transition: all .5s ease 0s;
    }
  }
}
.contacts__mail {
  span {
    background: url("../img/svg/mail.svg") left center no-repeat;
  }
}
.contacts__address {
  span {
    background: url("../img/svg/geolocation.svg") left center no-repeat;
    background-size: contain;
  }
}

.contacts__map {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 400px;
  max-height: 500px;
  box-sizing: border-box;
}
.map__link {
  display: block;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  position: absolute;
}
.map__circle {
  display: block;
  position: relative;
  z-index: -1;
  box-sizing: border-box;

  &::before {
    display: block;
    background: radial-gradient(circle, transparent 10%, $background 50% );
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
}
.map_img {
  width: 300px;
  height: 300px;
}

.contacts__developer {
  text-align: center;
  margin-bottom: 1em;
}

@media (min-width: 400px){
  .map_img {
    width: 400px;
    height: 400px;
  }
}
@media (min-width: 500px){
  .map_img {
    width: 500px;
    height: 500px;
  }
}
@media (min-width: 768px){
  .contacts__content {
    width: 100%;
    justify-content: space-around;
  }
  .contacts__info {
    width: 500px;
    font-size: px-to-rem(24px);
    line-height: px-to-rem(28px);
  }
}

@media (min-width: 885px) {
  .contacts__content {
    margin-bottom: 6em;
  }
}

@media (min-width: 930px) {

  .contacts__info{
    margin-bottom: 0;
    p {
      padding: .5em 0;
    }
  }
}
@media (min-width: 1024px) {
  .contacts__map {
    margin-left: 3em;
  }
}

@media (min-width: 1500px) {

  .contacts__map {
    margin-left: 6em;
  }
}
@media (min-width: 1600px) {
}

@media (max-height: 768px) and (min-width: 1024px) {
  .contacts__info {
    width: 350px;
    height: 350px;
    font-size: px-to-rem(16px);
    p {
      padding: 0;
    }
  }
  .contacts__map {
    width: 350px;
    height: 350px;
    margin-left: 5em;
  }
}

@media (max-height: 598px) {
  .title {
    padding-top: 1em;
    margin-bottom: .5em;
  }
  .contacts__content {
    margin-bottom: 3em;
  }
  .contacts__info {
    width: 300px;
    height: 300px;
    font-size: px-to-rem(14px);
    margin-bottom: 0;
  }
  .contacts__map {
    width: 300px;
    height: 300px;
  }
  .map_img {
    width: 400px;
    height: 400px;
  }
}