  .fixed__menu .fixed__menu_hamburger {
    display: block;
    width: 2rem;
    height: 2rem; }
  .fixed__menu #menu__toggle {
    opacity: 0;
    position: absolute; }
  .fixed__menu #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
    transform-origin: 90% 90%; }
  .fixed__menu #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0); }
  .fixed__menu #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    width: 32px;
    transform: rotate(90deg); }
  .fixed__menu #menu__toggle:checked ~ .menu__box {
    visibility: visible;
    left: 0; }
  .fixed__menu .menu__btn {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 32px;
    width: 32px; }
  .fixed__menu .menu__btn > span,
  .fixed__menu .menu__btn > span::before,
  .fixed__menu .menu__btn > span::after {
    position: absolute;
    display: block;
    width: 32px;
    height: 3px;
    bottom: 8px;
    background-color: #fff;
    transition-duration: .25s; }
  .fixed__menu .menu__btn > span::before {
    content: '';
    top: -8px;
    width: 28px;
    right: 0; }
  .fixed__menu .menu__btn > span::after {
    content: '';
    top: -16px;
    width: 26px;
    right: 0; }
  .fixed__menu .menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 120px 0 1em 0;
