//Title
.title {
  font-size: px-to-rem(32px);
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  padding: .3em;
  margin: 0 0 .7em;
  color: $active;
}
.subtitle {
  margin: .5em 0;
  padding: 0;
  text-align: center;
  line-height: 1.5em;
  font-size: px-to-rem(12px);
}
.section__sto,  .section__shop {
  .title {
    line-height: 1.15em;
    margin: 0;
  }
}
.section__shop {
  .subtitle {
    margin-bottom: 1.5em;
  }
}

.section__sto, .section__club {
  ._icon-logo_service::before,
  ._icon-club::before {
    transform: translate(0, 25%);
    padding-right: .25em;
    font-size: 1.5em;
    text-align: center;
  }
  ._icon-logo_service::before {
    transform: translate(0, 15%);
  }
}
@media (min-width: 480px) {
  .title {
    font-size: px-to-rem(48px);
  }
  .subtitle {
    font-size: px-to-rem(16px);
  }
}

@media (min-width: 1024px) {
  .title {
    font-size: px-to-rem(56px);
    padding: 1em .5em 0;
  }
}
@media (min-width: 1200px) {
  .title {
    margin: .5em 0;
  }
  .subtitle {
    margin-bottom: 1em;
  }
}

@media (min-width: 1850px) {
  .title {
    font-size: px-to-rem(72px);
    margin-top: 0;
    padding-top: .5em;
  }
}

@media (max-height: 768px) and (min-width: 1024px) {
  .title {
    padding-top: 1em;
    margin-bottom: .5em;
  }
}
