@font-face {
  font-family: 'icon';
  src:  url('../fonts/icon.eot?k7uxsj');
  src:  url('../fonts/icon.eot?k7uxsj#iefix') format('embedded-opentype'),
  url('../fonts/icon.ttf?k7uxsj') format('truetype'),
  url('../fonts/icon.woff?k7uxsj') format('woff'),
  url('../fonts/icon.svg?k7uxsj#icon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="_icon-"]::before, [class*=" _icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  display: inline-block;
  font-family: 'icon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

