.top__menu_wrap {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  max-height: 50px;
  padding: 0 15px;
  background-color: $background;

  a {
    transition: all .5s ease 0s;
    &:hover, &:focus {
      background-color: $active;
      transition-property: all;
      transition-duration: .5s;
    }
  }
}
p {
  margin: .5em 0;
}

.top__menu_language {
  display: flex;
  align-items: center;
  justify-content: center;
}

.dropbtn {
  padding: 5px;
  border: none;
  cursor: pointer;
  background-color: inherit;
  color: inherit;
  outline: none;
  font-style: italic;

  &:hover, &:focus {
    background-color: $active;
  }
}

.dropdown {
  position: relative;
  display: inline-block;

  .dropdown__content {
    display: none;
    position: absolute;
    background-color: $light-active;
    overflow: auto;

    a {
      display: block;
      padding: 0.5rem 1.3rem;

    }

  }
}

.top__menu_days, .top__menu_phone {
  display: none;
}

@media (min-width: 640px ) {
  .top__menu_wrap {
    justify-content: space-between;

    .top__menu_days, .top__menu_phone {
      display: block;
    }
  }

}