.section__club {
  background: url("../img/svg/club.svg") center no-repeat ;
  background-size: contain;
  font-size: px-to-rem(24px);
  line-height: px-to-rem(60px);
  position: relative;
  padding-top: 1em;
  z-index: 1;

  &::before {
    background: $background;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    opacity: 0.9;
  }
}

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2.5em;
  }

  .section__ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 2.5em;
  }

  .section__li {
    position: relative;
    padding-left: 1em;

    &::before {
      position: absolute;
      content: "";
      width: .3em;
      height: .4em;
      background-color: $active;
      left: .2em;
      top: 1em;
      transform: skewX(-41.5deg);

    }
  }

  .club_gallery {
    margin: 0;
    padding: 0;
    width: 16rem;
    height: auto;
    margin: 0 auto 1em;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
     iframe {
       border: 0;
       width: 16rem;
       height: 9rem;
     }
  }



@media (min-width: 640px ) {
  .section__club {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }
}

@media (min-width: 1024px) {
  .section__club {
    background: url("../img/svg/logo-jeep.svg") center no-repeat;
    background-size: contain;
    padding-top: 1em;

    .content {
      flex-direction: row-reverse;
      align-items: center;

      .club_gallery {
        margin: 0 2em;
        width: 24rem;
        height: 13.5rem;
        iframe {
          width: 234rem;
          height: 13.5rem;
        }
      }
    }

    .section__ul {
      font-size: px-to-rem(36px);
      line-height: px-to-rem(52px);
    }

    .section__li {
      &::before {
        top: .5em;
      }
    }

  }
}

@media (min-width: 1200px) {
  .section__club{
    .content {
      .club_gallery {
        width: 30rem;
        height: 16rem;
        margin-right: 3em;
        iframe {
          width: 28rem;
          height: 15.75rem;
        }
      }
    }
  }

}

@media (min-width: 1400px) {
  .section__club{
    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }

  }
}

@media (min-width: 1500px) {
  .section__club {
    margin-bottom: 4em;
    .content {
      .club_gallery {
        width: 35rem;
        height: 19rem;
        margin-right: 6em;
        iframe {
          width: 35rem;
          height: 19.7rem;
        }
      }
    }

    .section__ul {
      font-size: px-to-rem(42px);
      line-height: px-to-rem(64px);
    }
  }
}


@media (min-width: 1700px) {
  .section__club {
    margin-bottom: 6em;
  }
}